.calendar-container {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: left;
  padding: 25px;
}

.calendar-container .year>span{
  border-bottom: 1px solid gray;
  font-size: 24px;
  font-weight: bold;
}

.calendar-container .year .year-container {
  margin-top: 25px;
  margin-bottom: 25px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.calendar-container .year .year-container .month {
  padding: 10px;
  width: calc(100% * (1/4) - 25px);
  text-align: center;
}

.calendar-container .year .year-container .month span {
  font-weight: bold;
  line-height: 34px;
}

.calendar-container .year .year-container .month .week {
  display: -ms-flexbox;
  display: flex;
}

.calendar-container .year .year-container .month .week .day {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 50px;
  background: yellow;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  border: 1px solid white;
}

.calendar-container .year .year-container .month .week .day.label {
  background: white;
}

.calendar-container .year .year-container .month .week .day.blank {
  background: lightgray;
}

.calendar-container .year .year-container .month .week .day.holiday {
  background: red;
  cursor: pointer;
}



